.section {
	position: relative;
	padding: 2em;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	background-color: var(--background_color_accent);
}
.preview_image {
	flex-basis: 100%;
	padding-top: 56.25%;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	transition-property: flex-basis, padding, margin;
	transition-duration: 0.3s;
	transition-timing-function: ease-in-out;
	margin: 2em 0 0;
}
.content {
	position: relative;
	z-index: 10;
	flex-basis: 100%;
	text-align: left;
	padding: 2em;
	background-color: var(--background_color);
	transition-property: flex-basis, padding, margin;
	transition-duration: 0.3s;
	transition-timing-function: ease-in-out;
}
.content > * {
	margin-top: 1em;
	transition: margin 0.3s ease-in-out;
}
.content > :first-child {
	margin-top: 0;
}
.title {
	font-weight: bold;
	font-size: 1.5em;
	transition: font-size 0.3s ease-in-out;
}
.headline {
}
.read_more_action {
	display: flex;
	align-items: center;
	font-weight: bold;
}
.read_more_icon {
	margin-left: 1em;
}
@media (min-width: 48rem) {
	.section {
		padding: 3em 5em;
	}
	.preview_image {
		flex-basis: calc(50% + 2em);
		padding-top: 0;
		margin: 2em 0 2em -2em;
	}
	.content {
		flex-basis: 50%;
		padding: 4em 2em;
		margin-top: 0;
		text-align: center;
	}
	.title {
		font-size: 2em;
	}
	.read_more_action {
		justify-content: center;
	}
}
@media (min-width: 64rem) {
	.section {
		padding: 5em;
	}
	.content {
		padding: 5em;
	}
	.preview_image {
		flex-basis: calc(50% + 3em);
		padding-top: 0;
		margin: 3em 0 3em -3em;
	}
	.content > * {
		margin-top: 2em;
	}
	.title {
		font-size: 3em;
	}
}



